const FlightTransfer = ({ transferType, transferDes, isMultiFlight }) => {
    return (
        <View className={`flit_arrow ${transferType ? (isMultiFlight ? 'turn_more' : 'turn') : ''}`}>
            {!isMultiFlight && transferType ? (
                <>
                    <View className="flit_turn">{transferType}</View>
                    <View className="flit_tcity">{transferDes}</View>
                </>
            ) : null}
            {isMultiFlight && transferType ? (
                <Text className="flit_turn">
                    {transferType}
                    {transferDes}
                </Text>
            ) : null}
        </View>
    )
}

const FlightBaseInfo = ({ segment }) => {
    const { briefFlight = {} } = segment
    const {
        crossDay,
        arriveTime,
        arriveAirport,
        departTime,
        departAirport,
        transferInfo = [],
        transferType,
        transferDes,
        departTerminal,
        arriveTerminal,
        airlineCode,
        stopStations = [],
        isAirLineNameHold,
        isAirlineTimeHold
    } = briefFlight

    const isMultiFlight = transferInfo.length + stopStations.length > 1
    return (
        <>
            {crossDay > 0 && <sub className="j1">+{crossDay}天</sub>}
            {crossDay < 0 && <sub className="j1">{crossDay}天</sub>}
            <View className="flit_info">
                <View className="flit_pic">
                    <Image
                        style={{
                            maxWidth: '24px',
                            maxHeight: '24px',
                        }}
                        src={`//pic.c-ctrip.com/flight_intl/airline_logo/40x35/${airlineCode}.png`}
                    />
                </View>
                <FlightDepartArrive
                    depTime={departTime}
                    departAirport={departAirport}
                    beginTermin={departTerminal}
                    isAirlineTimeHold={isAirlineTimeHold}
                />
                <FlightTransfer
                    transferType={transferType}
                    transferDes={transferDes}
                    isMultiFlight={isMultiFlight}
                />
                <FlightDepartArrive
                    depTime={arriveTime}
                    departAirport={arriveAirport}
                    crossDay={crossDay}
                    beginTermin={arriveTerminal}
                    isAirlineTimeHold={isAirlineTimeHold}
                />
            </View>
        </>
    )
}

const FlightLineNameInfo = ({ segment }) => {
    const { briefFlight = {}, transferInfoList = {}, specialChangeTextDesc = '' } = segment
    const { stopList = [], transferList = [] } = transferInfoList
    const transferStopList = transferList.concat(stopList)
    const transferStopCounts = transferStopList.length
    const { duration, isCarrier, airLineName = [], flightNo = [], directFlightType } = briefFlight

    return (
        <View className="flit_corp">
            <View className="flit_corp_inner">
                <Text className="flit_time">
                    <Text className="toclip" style={{ verticalAlign: 'top' }}>
                        {airLineName[0]}
                        {flightNo[0]}
                        {isCarrier ? '(共享)' : ''}
                    </Text>
                    {(airLineName.length > 1 || flightNo.length > 1) && <em>等</em>}
                </Text>
                {!!directFlightType && <Text className="flit_time">{directFlightType}</Text>}
                <Text
                    className={classnames('flit_time', {
                        add_after: transferStopCounts > 0
                    })}
                >
                    {duration}
                </Text>
                {transferStopCounts > 0 &&
                    transferStopList.map((transferObj, index) => {
                        const {
                            cityName = '',
                            duration: transferDuration = '',
                            flightId
                        } = transferObj
                        return (
                            <Text className="flit_transfer" key={cityName}>
                                {`${flightId ? '中转' : '经停'}${cityName}${transferDuration}${
                                    transferStopCounts !== index + 1 ? '，' : ''
                                }`}
                            </Text>
                        )
                    })}

                {specialChangeTextDesc ? (
                    <Text className="flit_specialChangeTextDesc">{specialChangeTextDesc}</Text>
                ) : null}
            </View>
        </View>
    )
}

